body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.modal-90w {
  width: '90%';
  color: blue;
}



h1, h2 {
  color: #4a4a6c;
  padding: 2%;
  border-bottom: 4px solid #844158;
}


#projects, #education, #aboutMe {
  padding: 4rem 2rem 2rem 2rem;
}

#content {
  margin-left: 200px;
  overflow: scroll;
}

#profile {
  display: block;
  margin: auto;
  width: 80%;
  padding: 2% 0;
}

.footer {
  font-size: 90%;
  color: #4a4a6c;
  padding: .5rem;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
  /* margin-top: .5rem; */
  /* position: fixed; */
  /* bottom: 0;
  left: 0;
  width: 100%; */
}
.footer a {
  color: #4a4a6c;
}

.footer p {
  flex: 50% 1;
  border: 0;
  margin: 1em 0 0 0;
}

.link {
  color: #4a4a6c ;
}
.link:hover {
   color: #00F
}

